@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
*,::before,::after{box-sizing:border-box}body{height:100vh;background:#202020;color:whitesmoke;display:flex;justify-content:center;font-family:'Nunito', sans-serif}form{display:grid;place-items:center;margin-top:60px}form label+label{margin-top:2rem}form label select,form label input{padding:3px 5px;margin:0 0 0 10px;border:1px solid whitesmoke;border-radius:7px;font-family:'Nunito', sans-serif;background:#202020;color:whitesmoke}form label select{cursor:pointer}form .dynamic-color{display:grid;place-items:center;margin-top:60px}form .dynamic-color div{display:grid;place-items:center;width:150px;height:150px;border:3px whitesmoke solid;border-radius:50%;transition:background .7s;position:relative;text-align:center}form .dynamic-color div h3{position:absolute;width:100%;white-space:nowrap;word-wrap:hidden;overflow:hidden;color:#a09f9f}

